.content {
  width: 100%;
  box-sizing: border-box;
}

.content_page_form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 180px;
}

.content_page_notfound {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 300px;
}

.content_page_ingredient {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 120px;
}

.content_page_profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

.content_page_order_info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 122px;
}

.link {
  text-decoration: none;
  color: #4c4cff;
  transition: opacity 0.3s ease-in-out;
}

.link:hover {
  opacity: 0.7;
}

.error {
  color: #e52b1a;
}
